export default {
	data() {
		return {
			seconds: 10,
			codeBtn: {
				text: '获取验证码',
				waitingCode: false,
				count: this.seconds,
			},
			changeSuccess: false,
			disableCodeBtn: false,
			phone: null,
			msgCode: null,
			npwd: null,
			sessionId: null
		}
	},
	onLoad() {

	},
	methods: {
		sendCode: function() {
			this.codeBtn.waitingCode = true;
			this.codeBtn.count = this.seconds;
			this.codeBtn.text = this.codeBtn.count + 's';
			this.disableCodeBtn = true;

			uni.request({
				url: this.prefix + "/getMsgCode",
				data: {
					smsMob: this.phone
				},
				success: (res) => {
					if (res.data.code === 200) {
						this.sessionId = res.data.msg
						uni.showToast({
							title: "发送成功"
						})
					} else {
						uni.showToast({
							title: res.data.msg,
							icon: "none"
						})
					}

				}
			})

			let countdown = setInterval(() => {
				this.codeBtn.count--;
				this.codeBtn.text = this.codeBtn.count + 's';
				if (this.codeBtn.count < 0) {
					clearInterval(countdown);
					this.codeBtn.text = '重新发送';
					this.codeBtn.waitingCode = false;
					this.disableCodeBtn = false;
				}
			}, 1000);
		},
		gotoLogin: function() {
			uni.navigateTo({
				url: './login'
			})
		},
		doSubmit() {
			uni.request({
				url: this.prefix + "/doForget",
				data: {
					mphone: this.phone,
					npwd: this.npwd,
					msgCode: this.msgCode,
					sessionId : this.sessionId
				},
				success: (res) => {
					if (res.data.code === 200) {
						this.changeSuccess = !this.changeSuccess
					} else {
						uni.showToast({
							title: res.data.msg,
							icon: "none"
						})
					}
				}
			})
		}
	},
}
